const options = [
  {
    name: ":  ",
    value: "newest",
  },
  {
    name: ":  ",
    value: "older",
  },
  {
    name: ":  ",
    value: "cheaper",
  },
  {
    name: ":  ",
    value: "expensive",
  },
];

export default function Select({ onChange }) {
  return (
    <select onChange={(e) => onChange(e.target.value)}>
      {options.map((option, index) => (
        <option value={option.value} key={index}>{option.name}</option>
      ))}
    </select>
  );
}
